/****/
.container,body {
  background-color: transparent;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  height: 100vh;
  font-family: "Open Sans";
}

.card {
  width: 284px;
  height: 550px;
  background: #fff;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  box-shadow: 0 14px 26px rgba(0, 0, 0, 0.04);
  transition: all 0.3s ease-out;
  text-decoration: none;
  margin: 60px;
  padding: 30px;
}

.card p {
  font-size: 17px;
  color: #4c5656;
  margin-top: 20px;
  z-index: 1000;
  transition: color 0.3s ease-out;
  word-wrap: wrap;
  word-break: break-all;
}

.card p a {
  padding-left: 24px;
  padding-right: 10px;
  color: #4c5656;
  display: inline-block;
}

